import { memo, useRef } from 'react';
import { IndexBar, List } from 'antd-mobile';
import { IndexBarRef } from 'antd-mobile/es/components/index-bar';
import './index.less';

const Detail = () => {
  const indexBarRef = useRef<IndexBarRef>(null);
  const localData = [
    {
      title: '2022年',
      items: [
        {
          title: '这是个美丽的秋天',
        },
        {
          title: '这是个美丽的夏天',
        },
        {
          title: '这是个美丽的春天',
        },
      ],
    },
    {
      title: '2021年',
      items: [
        {
          title: '这是个美丽的秋天',
        },
        {
          title: '这是个美丽的夏天',
        },
        {
          title: '这是个美丽的春天',
        },
      ],
    },
    {
      title: '2020年',
      items: [
        {
          title: '这是个美丽的秋天',
        },
        {
          title: '这是个美丽的夏天',
        },
        {
          title: '这是个美丽的春天',
        },
      ],
    },
    {
      title: '2015年',
      items: [
        {
          title: '这是个美丽的秋天',
        },
        {
          title: '这是个美丽的夏天',
        },
        {
          title: '这是个美丽的春天',
        },
      ],
    },
    {
      title: '2014年',
      items: [
        {
          title: '这是个美丽的秋天',
        },
        {
          title: '这是个美丽的夏天',
        },
        {
          title: '这是个美丽的春天',
        },
      ],
    },
    {
      title: '2013年',
      items: [
        {
          title: '这是个美丽的秋天',
        },
        {
          title: '这是个美丽的夏天',
        },
        {
          title: '这是个美丽的春天',
        },
      ],
    },
    {
      title: '2012年',
      items: [
        {
          title: '这是个美丽的秋天',
        },
        {
          title: '这是个美丽的夏天',
        },
        {
          title: '这是个美丽的春天',
        },
      ],
    },
    {
      title: '2011年',
      items: [
        {
          title: '这是个美丽的秋天',
        },
        {
          title: '这是个美丽的夏天',
        },
        {
          title: '这是个美丽的春天',
        },
      ],
    },
  ];
  return (
    <div className="detail_container">
      <IndexBar ref={indexBarRef}>
        {localData.map((localDataItem, localDataIndex) => {
          const { title, items } = localDataItem;
          return (
            <IndexBar.Panel
              index={localDataIndex + ''}
              title={title}
              key={title}>
              <List>
                {items.map((item: any, index: number) => (
                  <List.Item key={index}>{item.title}</List.Item>
                ))}
              </List>
            </IndexBar.Panel>
          );
        })}
      </IndexBar>
    </div>
  );
};
export default memo(Detail);
